import * as _ from "../prompts.json";
//TODO: more funny messages here

export default {
  methods: {
    getErrorMessage() {
      const index = Math.round(
        Math.random() * (_.errorMessages.length - 1 - 0) + 0
      );

      return _.errorMessages[index];
    },
    getStyledErrorMessage() {
      const index = Math.round(
        Math.random() * (_.styledErrorMessages.length - 1 - 0) + 0
      );

      return _.errorMessages[index];
    }
  }
};
